.gallery {
    position: relative;
    
    width: 100%;
    
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;

    gap: $purple_space;

    @media (max-width: map.get($screens, "mobile")) {
        gap: $purple_space * .25;
        grid-template-columns: 1fr 1fr;
    }
}

.gallery .item-card {
    width: 100%;
    height: 0;
    padding-bottom: 100%;
    position: relative;
    background-color: gray;
}

.gallery .item-card .hidden-preview {
    opacity: 0%;
    width: 100%;
    height: 100%;
    color: white;
    transition: $default_trans_interval;
}

.gallery .item-card .hidden-preview .title {
    position: absolute;
    float: left;
    top: 50%;
    left: 50%;
    text-align: center;
    transform: translate(-50%, -50%);
    line-height: 100%;
    font-size: 4rem;
    font-weight: bold;
    hyphens: manual;

    @media screen and (max-width: map.get($screens, "medium")) {
        font-size: 2rem;
    }
}

.gallery .item-card .hidden-preview .title-holder {
    position: absolute;
    background-color: rgba(0, 0, 0, 0.5);
    width: 100%;
    height: 100%;
}

.gallery .item-card .hidden-preview:hover {
    opacity: 100%;
    background-color: gray;
    position: absolute;
}

.gallery .item-card .cover {
    width: 100%;
    height: 100%;
    position: absolute;
}

.gallery .item-card img {
    object-fit: cover;
    object-position: center;
    height: 100%;
    width: 100%;
}