.topbar,
.topbar.expanded,
.topbar .content,
.topbar.expanded .content,
.topbar .drawer,
.topbar .item.route,
.topbar.expanded .brand {
    transition: $default_trans_interval;
}

.topbar {
    padding-top: 1rem;
    padding-bottom: 1rem;
}

.topbar .content {
    display: flex;
    justify-content: space-between;
}


.topbar .drawer {
    display: none;
    color: map.get($colors, "onPrimary");
    font-size: $icon_font_size;
    line-height: $blue_space;
}

.topbar .brand,
.topbar .leading,
.topbar .trailing {
    display: inline-block;
    height: $blue_space;
}

.topbar .brand,
.topbar .brand .brand-img {
    height: $blue_space;
}

.topbar .leading .item.route {
    text-decoration: none;
    line-height: $blue_space;

    padding-right: 1rem;

    color: map.get($colors, "onPrimary");
    transition: $default_trans_interval;
}

.topbar .leading .item.route:hover {
    color: map.get($colors, "onPrimaryOp5")
}

.topbar .leading .item.route.active {
    font-weight: 800;
}

.topbar .trailing .item.social {
    position: relative;
    font-size: $icon_font_size;
    text-align: center;

    display: inline-block;

    margin: 0.1rem;

    width: $blue_space;
    height: $blue_space;
    padding: 0;
}

.topbar .trailing .item.social i {
    padding-top: 0;
    padding-bottom: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

// Collapsed
@media screen and (max-width: 900px) {
    .topbar .leading,
    .topbar .trailing {
        display: none;
    }

    .topbar .drawer {
        display: block;
    }

    .topbar .content {
        margin-left: 1rem;
        margin-right: 1rem;
    }

    .topbar .content .brand {
        margin: auto;
    }
}

// Expanded
@media screen and (max-width: 900px) {
    .topbar.expanded .content {
        display: block;
    }

    .topbar.expanded .brand {
        display: none;
    }

    .topbar.expanded .leading,
    .topbar.expanded .trailing {
        display: block;
    }

    .topbar.expanded .leading .item.route {
        text-decoration: none;
        display: block;
        padding-bottom: 1rem;
        padding-top: 1rem;
    }

    .topbar.expanded .leading,
    .topbar.expanded .trailing {
        height: auto;
    }
}