$font-title-size: 1.85rem;
$font-title-spacing: 0.90 * $font-title-size;
$font-description-size: 1.30rem;
$font-description-spacing: 0.95 * $font-description-size;

#banners {
    margin-bottom: $red_space;
}

.banner {
    width: 100%;

    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: $purple_space;
    margin-bottom: $purple_space;
    height: $carousels_height;
}

#banners .card,
#banners .card .content,
#banners .cover {
    border-radius: $card_radius;
}

#banners .cover {
    object-fit: cover;
    object-position: center;
    height: $carousels_height;
    width: 100%;
}

#banners .card {
    position: relative;
    padding: 1.5 * $blue_space;
}

#banners .card .title {
    display: block;
    font-weight: 800;
    font-size: $font-title-size;
    line-height: $font-title-size;
    margin-bottom: $blue_space;
}

#banners .card .description {
    display: block;
    margin-bottom: $blue_space;
    line-height: $font-description-spacing;
    font-size: $font-description-size;
}

#banners .rounded-button {
    display: inline-block;
    // max-width: 80%;
}

#banners .card .content {
    margin: 0;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
}


@media screen and (max-width: 1000px) {
    #banners .rounded-button {
        width: 100%;
    }
}

@media screen and (max-width: 900px) {
    #banners .card {
        padding: $blue_space;
    }

    #banners .card .content {
        position: relative;
    }
}

@media screen and (max-width: 700px) {
    .banner {
        grid-template-columns: 1fr 0fr;
        gap: 0;
        height: auto;
    }

    #banners .cover {
        display: none;
    }

    #banners .card {
        padding: 0;
        background-size: cover;
        border-style: none;
        // background image is being set in 
        // the generated file banners.css
    }

    #banners .content {
        width: 100%;
        height: 100%;
        padding: 1.5 * $blue_space;
        backdrop-filter: brightness(50%) blur(6px);
        -webkit-backdrop-filter: brightness(50%) blur(6px);
        color: white;
    }

    #banners .rounded-button {
        color: white;
        border-color: white;
    }

    #banners .rounded-button:hover {
        background-color: white;
        color: black;
    }
}

