#contact {
  margin-left: 3 * $red_space;
  margin-right: 3 * $red_space;

  @media screen and (max-width: map.get($screens, "large")) {
    margin-left: $red_space;
    margin-right: $red_space;
  }

  @media screen and (max-width: map.get($screens, "mobile")) {
    margin-left: calc($red_space * 0.5);
    margin-right: calc($red_space * 0.5);
  }

  .section-title {
    @media screen and (max-width: map.get($screens, "medium")) {
      display: none;
    }
  }

  h2 {
    margin-bottom: $blue_space;
  }

  #contact-body {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: $red-space;
    align-items: center;

    @media screen and (max-width: map.get($screens, "medium")) {
      display: inline-block;
      flex-direction: column;
      gap: 1rem;
    }
  }

  .textual {
    order: 2;

    @media screen and (max-width: map.get($screens, "medium")) {
      order: 0;
      margin: 2rem 0;
      position: relative;
    }
  }

  #form {
    background-color: #EAEAEA;
    border-radius: $card_radius;
    padding: $red-space;
    position: relative;
    overflow: hidden;
    height: 55vh;

    @media screen and (max-width: map.get($screens, "medium")) {
      width: 100%;
    }

    button {
      display: none;
    }

    button.active {
      display: block;
    }

    .form-content {
      position: relative;
    }

    .step {
      display: none;
    }

    .step.active {
      display: block;
      animation-name: opacity-increase;
      animation-duration: .3s;
    }

    .rounded-button.filled {
      border: none;
    }

    #btn-section {
      position: absolute;
      top: 85%;
      transform: translateY(-100%);
      display: flex;
      gap: 1rem;
    }

    .item {
      margin-bottom: $purple-space;
      width: 100%;

      .check, .radio {
        width: 100%;

        input {
          width: auto;
          border-color: black;
          background-color: transparent;
          margin: 0.5rem 0.5rem 0.5rem 0;
        }

        label {
          font-weight: 400;
          width: auto;
        }

        input:checked + label {
          font-weight: bold;
          color: black;
        }
      }

      legend,
      label {
        font-size: 1.2rem;
        font-weight: bold;
      }

      input {
        padding: 0.75rem;
        font-size: 1.3rem;
        background-color: transparent;
        border-style: none none solid none;
        width: 100%;
        outline: none;
        border-width: 0.2rem;

        border-radius: $card_radius $card_radius 0 0;
      }

      input:focus {
        border-color: black;
        background-color: rgba(0, 0, 0, 0.121);
      }
    }

    .item.wrong input {
      border-color: rgb(118, 0, 0);
      background-color: rgba(118, 0, 0, 0.121);
    }

  }
}